* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  font-size: 20px;
}

body {
  background-color: #fff59d;
}

.inline {
  display: inline;
}

.header {
  background-color: #1b3039;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 85px;
  padding: 5px 10%;
}

.logo {
  cursor: pointer;
  width: 40;
  height: 40;
  display: flex;
}

.logo-desc {
  color: white;
  margin-left: 20;
  padding-left: 20;
}

.header .logo img {
  height: 100px;
  transition: all 0.3s;
  margin-right: 15;
}

.header .logo img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transform: rotate(-15deg);
          transform: rotate(-15deg);
}

.header .nav-links {
  list-style: none;
}

.header .nav-links li {
  display: inline-block;
  padding: 0 20px;
}

.header .nav-links li:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.header .nav-links a {
  font-size: 700;
  color: #eceff1;
  text-decoration: none;
}

.header .nav-links li a:hover {
  color: #ffbc0e;
}

/* --------------------------------------------- */
.header .btn button {
  font-weight: 700;
  color: #1b3039;
  padding: 9px 25px;
  background: #eceff1;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.header .btn button:hover {
  background-color: #e2f1f8;
  color: #ffbc0e;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

